<template>
    <div v-bind:class="activeClass">
        <header>
            <span @click="routerPush(btnList[0])">首页</span>
            <h1>{{activeTitle}}</h1>
        </header>
        <nav>
            <ul>
                <li :class="{'active':item.className==activeClass}" v-for="(item,index) in btnList" :key='index' @click="routerPush(item)">{{item.name}}</li>
            </ul>
        </nav>
    </div>
</template>
<script>
export default {
    data(){
        return{
        activeTitle:'电影',
        activeClass:"movie",
        btnList:[
            {
                name:"电影",
                path:"/",
                className:'movie'
            },
            {
                name:"音乐",
                path:"/music",
                className:'music'
            },
            {
                name:"聊天",
                path:"/talk",
                className:'talk'
            },
            {
                name:"新闻",
                path:"/news",
                className:'news'
            } 
        ]
        }
    },
    methods: {
        routerPush(obj){
            this.$router.push(obj.path);
            this.activeTitle=obj.name;
            this.activeClass=obj.className;

        }
    },
    props: ['navVal'],
    computed: {
    propsVal(){
      // this.activeClass = this.navVal.className;
      // this.activeTitle = this.navVal.title
      return this.navVal.title
    }
  },
  watch: {
    propsVal(){
      this.activeClass = this.navVal.className;
      this.activeTitle = this.navVal.title
    }
  },
}
</script>
<style scoped>
 header,nav{
    color: #fff;
    position: fixed;
    width: 100%;
    height: 1rem;
    background-color:rgb(33, 150, 243);
    text-align: center;
  }
  .movie header,.movie nav{
    background-color: rgb(33, 150, 243);
  }
  .music header,.music nav{
    background-color: rgb(0, 150, 136);
  }
  .book header,.book nav{
    background-color: rgb(121, 85, 72);
  }
  .talk header,.talk nav{
    background-color: rgb(63, 81, 181);
  }
  header{
    top:0;
    right: 0;
    left: 0;
  }
  header span{
    position: absolute;
    left: 10px;
    top:50%;
    transform: translateY(-50%);
  }
  nav{
    bottom: 0;
    right: 0;
    left: 0;
  }
  nav ul{
    width: 100%;
  }
  nav li{
    color: #ddd;
    float: left;
    width: 25%;
    height: 1rem;
    line-height: 1rem
  }
  nav li.active{
    color: #fff;
    font-weight: bolder;
    font-size: 16px;
  }
</style>
